// src/pages/interview.js
import Head from "next/head";

import { useState } from "react";
import Image from "next/image";
import { questions } from "@/data/questions";

export default function InterviewPage() {
    const [step, setStep] = useState(1);
    const [formData, setFormData] = useState({
        fullName: "",
        email: "",
        phone: "",
        linkedin: "",
        answers: {}, // Now stores arrays for multi-select
        resume: null,
        coverLetter: null,
    });
    const [currentQuestion, setCurrentQuestion] = useState(0);

    const firstPageQuestions = questions.slice(0, 5); // First 5 questions

    const handleNext = () => {
        if (step === 1) setStep(2);
        else if (step === 2 && currentQuestion < firstPageQuestions.length - 1) {
            setCurrentQuestion(currentQuestion + 1);
        } else if (step === 2) setStep(3);
        else if (step === 3) {
            console.log("Form submitted:", formData);
        }
    };

    const handleBack = () => {
        if (step === 2 && currentQuestion > 0) setCurrentQuestion(currentQuestion - 1);
        else if (step > 1) setStep(step - 1);
    };

    const handleChange = (e) => {
        const { name, value, files } = e.target;
        if (files) {
            setFormData({ ...formData, [name]: files[0] });
        } else if (step === 1) {
            setFormData({ ...formData, [name]: value });
        }
    };

    const handleOptionToggle = (option) => {
        const currentAnswers = formData.answers[currentQuestion] || [];
        let newAnswers;
        if (currentAnswers.includes(option)) {
            newAnswers = currentAnswers.filter((ans) => ans !== option);
        } else {
            // For Q1 (languages), allow multiple; others are single-select
            newAnswers =
                currentQuestion === 0
                    ? [...currentAnswers, option]
                    : [option];
        }
        setFormData({
            ...formData,
            answers: { ...formData.answers, [currentQuestion]: newAnswers },
        });
    };

    const borderColors = [
        "border-red-500",
        "border-blue-500",
        "border-green-500",
        "border-yellow-500",
        "border-purple-500",
    ];

    return (
        <>
            <Head>
                <title>Growthpad Interview Process</title>
                <meta name="description" content="Complete your Growthpad interview" />
            </Head>
            <div className="min-h-screen bg-gray-100 flex flex-col justify-center items-center">
                <div className="max-w-3xl w-full mx-auto p-6 bg-white rounded-lg shadow-md">
                    {/* Logo */}
                    <div className="mb-6 text-center">
                        <Image
                            src="/assets/images/logo.svg"
                            alt="Growthpad Consulting Group Logo"
                            width={200}
                            height={200}
                            className="mx-auto"
                        />
                    </div>

                    {/* Step 1: Initial Form */}
                    {step === 1 && (
                        <div>
                            <h2 className="text-2xl font-bold text-[#231812] mb-4 text-center">
                                Let’s Get Started
                            </h2>
                            <div className="space-y-4">
                                <input
                                    type="text"
                                    name="fullName"
                                    value={formData.fullName}
                                    onChange={handleChange}
                                    placeholder="Full Name"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="email"
                                    name="email"
                                    value={formData.email}
                                    onChange={handleChange}
                                    placeholder="Email"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="tel"
                                    name="phone"
                                    value={formData.phone}
                                    onChange={handleChange}
                                    placeholder="Phone"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="text"
                                    name="linkedin"
                                    value={formData.linkedin}
                                    onChange={handleChange}
                                    placeholder="LinkedIn Profile (e.g., https://linkedin.com/in/...)"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                            </div>
                        </div>
                    )}

                    {/* Step 2: Questionnaire (First 5 Questions) */}
                    {step === 2 && (
                        <div>
                            {/* Progress Bar */}
                            <div className="flex justify-between items-center mb-1">
                                <p className="text-xs text-[#231812] font-semibold">
                                    {Math.round((currentQuestion / firstPageQuestions.length) * 100)}%
                                </p>
                                <p className="text-xs text-[#231812] font-semibold">
                                    Step {currentQuestion + 1} of {firstPageQuestions.length}
                                </p>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2 mb-6">
                                <div
                                    className="bg-[#f05d23] h-2 rounded-full"
                                    style={{
                                        width: `${(currentQuestion / firstPageQuestions.length) * 100}%`,
                                    }}
                                ></div>
                            </div>

                            {/* Single Question */}
                            <div className="space-y-6">
                                <div className="bg-white shadow-md rounded-lg p-4">
                                    <p className="text-center text-lg font-bold text-[#231812] mb-5">
                                        {firstPageQuestions[currentQuestion].text}
                                    </p>
                                    <div className="grid grid-cols-2 md:grid-cols-5 gap-4 place-items-center">
                                        {firstPageQuestions[currentQuestion].options.map((option, index) => {
                                            const isSelected = (formData.answers[currentQuestion] || []).includes(option);
                                            return (
                                                <div
                                                    key={option}
                                                    className="flex flex-col items-center cursor-pointer"
                                                    onClick={() => handleOptionToggle(option)}
                                                >
                                                    <div
                                                        className={`w-10 h-10 rounded-full flex items-center justify-center border-2 ${
                                                            borderColors[index % borderColors.length]
                                                        } ${isSelected ? "bg-[#f05d23] border-[#f05d23]" : "bg-white"}`}
                                                    >
                                                        {isSelected && (
                                                            <svg
                                                                viewBox="0 0 24 24"
                                                                className="w-6 h-6"
                                                                fill="#ffffff"
                                                            >
                                                                <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
                                                            </svg>
                                                        )}
                                                    </div>
                                                    <p className="text-xs font-semibold text-center mt-2 text-[#231812]">
                                                        {option}
                                                    </p>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Step 3: End Form */}
                    {step === 3 && (
                        <div>
                            <h2 className="text-2xl font-bold text-[#231812] mb-4 text-center">
                                Submit Your Documents
                            </h2>
                            <div className="space-y-4">
                                <div>
                                    <label className="block text-[#231812] mb-1">
                                        Resume (PDF or DOCX)
                                    </label>
                                    <input
                                        type="file"
                                        name="resume"
                                        accept=".pdf,.docx"
                                        onChange={handleChange}
                                        className="w-full p-2 border border-gray-300 rounded"
                                    />
                                </div>
                                <div>
                                    <label className="block text-[#231812] mb-1">
                                        Cover Letter (PDF or DOCX)
                                    </label>
                                    <input
                                        type="file"
                                        name="coverLetter"
                                        accept=".pdf,.docx"
                                        onChange={handleChange}
                                        className="w-full p-2 border border-gray-300 rounded"
                                    />
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Navigation */}
                    <div className="mt-6 flex justify-between">
                        <button
                            onClick={handleBack}
                            disabled={step === 1}
                            className="px-4 py-2 bg-gray-400 text-white rounded disabled:opacity-50 hover:bg-gray-500 transition"
                        >
                            Back
                        </button>
                        <button
                            onClick={handleNext}
                            disabled={step === 2 && !formData.answers[currentQuestion]?.length}
                            className="w-full md:w-auto px-4 py-2 bg-[#f05d23] text-white rounded hover:bg-[#d94f1e] disabled:bg-gray-300 disabled:text-gray-600 transition"
                        >
                            {step === 3 ? "Submit" : "Next"}
                        </button>
                    </div>
                </div>
            </div>
        </>
    );
}